$blue: #4698FF;
$orange: #ED4F24;

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

.blogsHr{
    border: 1px solid $blue;
    width: 80%;
    margin: 0;
}

.blogMother{
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .blogMain{
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 90%;

        .blogHead{
            color: $blue;
            font-size: 2.5rem;
           text-align: center;
            margin-bottom: 35px;
        }

        .postGrid{
            display: grid;
            width: 100%;
            grid-template-columns: repeat(3, 1fr);
            grid-template-rows: auto;
            max-width: 1200px;
            justify-content: center;
            justify-items: center;
            align-items: start;;
            grid-gap: 30px;
            
            .blogPost{
                display: flex;
                flex-direction: column;
                align-items: center;
                align-self: flex-start;
                text-align: center;
                width: 100%;
                border: #333 solid 1px;
                height: auto;
                border-radius: 5px;
                padding-bottom: 10px;
                box-shadow: 4px 4px $orange, 8px 8px $blue;

                .blogTitle{
                    background: $blue;
                    color: #ffffff;
                    padding: 10px 0;
                    font-size: 1.3rem;
                }
                .blogText{
                    padding: 10px;
                    width: 90%;
                }

                .dateTimeDiv{
                    display: flex;
                    flex-direction: row;
                    padding: 10px;
                    justify-content: center;
                    
                    .blogDate{ 
                        &:before{
                            color: blue;
                            content: "\00a0  ||| \00a0";
                        }
                    }
                    .blogTime{}
                }


            }                 
            @media(min-width: 1450px) {
                max-width: 1500px;
                grid-gap: 50px;
                .blogPost{
                    max-width: 1400px;;

                        .blogTitle{
                            font-size: 1.8rem;
                        }

                        .dateTimeDiv{
                            .blogDate, .blogTime{
                                font-size: 1.1rem;
                            }
                        }  
                    }
                }
        }

    }
}